//---------------------------------------------sections
.section {
	position: relative;

	width: 100%;

	font-size: 1rem;

	@include bp($point_2, min) {
		padding: 7rem 13.6rem 7rem var(--gl-indent);
	}

	@include bp($point_3) {
		padding: 3.2rem var(--gl-indent);
	}

	.hero + & {
		@include bp($point_2, min) {
			margin-top: var(--full-screen);
		}
		// @include bp($point_3) {
		// 	margin-top: calc(var(--full-screen) - var(--height-header));
		// }
	}
}

.section_in {
	max-width: $page_width;
	width: 100%;

	@include bp($point_2, min) {
		max-width: 71vw;
	}

	.hero & {
		max-width: $page_2_width;
	}
}
