@import "family";
@import "reset";
@import "fonts";
@import "print";

//------------------------------------------------------------base styles
:root {
	--full-screen: calc(var(--vh, 1vh) * 100);

	--height-footer: 7.4rem;

	@include bp($point_2, min) {
		--gl-indent: 23.4rem;
		--height-header: 13rem;
	}

	@include bp($point_3) {
		--gl-indent: 7.3rem;
		--height-header: 9.9rem;
	}
}

html {
	height: 100%;

	@include bp(1920px, min) {
		font: 400 calc(100vw / 1920 * 10) / 1.33 $font_1;
	}

	@include bp(1919px, 1440px) {
		font: 400 calc(100vw / 1440 * 10) / 1.33 $font_1;
	}

	@include bp(1439px, $point_2) {
		font: 400 calc(100vw / 1300 * 10) / 1.33 $font_1;
	}

	@include bp($point_3, 415px) {
		font: 400 10px / 1.33 $font_1;
	}

	@include bp(414px, max) {
		font: 400 calc(100vw / 414 * 10) / 1.33 $font_1;
	}
}

body {
	position: relative;

	display: flex;
	height: 100%;

	font-size: $font_size_base;
	color: $color_text_base;

	-webkit-text-size-adjust: none;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;

	background: $body_bg;

	&.body--open_menu_state {
		position: fixed;
		left: 0;
		top: 0;

		width: 100%;
	}

	@supports (display: grid) {
		display: block;
	}
}

.preloader {
	position: fixed;
	top: 0;
	left: 0;
	width: 100%;
	height: 100vh;
	background-color: $white;

	opacity: 1;
	transition: opacity 0.3s;

	z-index: 1000;

	.body--loaded & {
		pointer-events: none;
		opacity: 0;
	}
}

.wrapper {
	position: relative;

	width: 100%;
	display: flex;
	flex-direction: column;
	min-height: 100%;
	height: auto !important;

	overflow-x: hidden;
	background-color: $white_3;
}

.base {
	width: 100%;
	position: relative;
	flex: 1 0 auto;

	padding: var(--height-header) 0 0 0;
}

a {
	text-decoration: none;
	color: $color_link_base;

	&:hover,
	&:focus {
		text-decoration: underline;
	}
}

//------------------------------------------------------------base styles###
