//------------------------------------------------------------layout
.header {
	@extend %transition_background;

	position: fixed;
	top: 0;
	left: 0;

	height: var(--height-header);
	width: 100%;

	font-size: 1rem;

	@include z-index(header);

	@include bp($point_2, min) {
		padding: 2.6rem 3.2rem 2.6rem 3.8rem;
	}

	@include bp($point_3) {
		padding: 2.1rem 2.2rem 2.6em;

		.body--home_mod & {
			background-color: $black_2;
		}

		&:not(.body--home_mod &) {
			background-color: $white_3;
		}
	}
}

//------------------------------------------------------------layout###
.header_in {
	@include flex_block(row, wrap, flex-start);
}

.header_logo {
	flex: 1 1 auto;

	&:not(:last-child) {
		margin-right: 2rem;
	}

	@include bp($point_2, min) {
		padding-top: 1.3rem;
	}
}

.header_logo__link {
	display: block;

	@include bp($point_2, min) {
		width: 15.9rem;
		height: 6.5rem;
	}

	@include bp($point_3) {
		width: 12.7rem;
		height: 5.2rem;
	}
}

.header_logo__text {
	@extend %transition_color;

	fill: currentColor;

	@include bp($point_2, min) {
		body:not(.body--scroll_state).body--home_mod & {
			color: $white;
		}

		.body--home_mod.body--scroll_state &,
		&:not(.body--home_mod &) {
			color: $black_2;
		}
	}

	@include bp($point_3) {
		.body--home_mod & {
			color: $white;
		}

		&:not(.body--home_mod &) {
			color: $black_2;
		}
	}
}

.header_logo__img {
	@extend %contain_img;
}

.header__body {
	@extend %transition_transform;

	@include bp($point_3) {
		position: fixed;
		top: 0;
		right: 0;
		z-index: 5;

		width: 100%;
		height: 100%;

		transform: translateX(100%);
		overflow-x: hidden;
		overflow-y: auto;

		.body--open_menu_state & {
			transform: translateX(0);
		}
	}
}

.header__content {
	@include bp($point_2, min) {
		@include flex_block(row, wrap, flex-start, center);
	}

	@include bp($point_3) {
		position: relative;

		min-height: 100%;
		width: 100%;

		padding: 2.1rem 2.2rem 3.1rem 9.5rem;

		&::before,
		&::after {
			content: "";
			position: absolute;
			top: 0;

			height: 100%;
		}

		&::before {
			left: 0;

			width: 7.3rem;

			opacity: .85;
			background-image: $gradient;
		}

		&::after {
			right: 0;
			z-index: -1;

			width: calc(100% - 7.3rem);

			background-color: $white;
		}

		@include flex_block(column-reverse, nowrap, flex-end);
	}
}

// menu
.header_menu {
	@extend %transition_transform;

	@include bp($point_2, min) {
		&:not(:last-child) {
			margin-right: 5.2rem;
		}
	}

	@include bp($point_3) {
		flex: 1 1 auto;
		width: 100%;

		padding: 13.5rem 0 0 3.9rem;
		margin-bottom: 2rem;

		@include flex_block(row, nowrap, center);
	}
}

.header_menu__list {
	@include bp($point_2, min) {
		margin: 0 -4.6rem -2rem 0;

		@include flex_block(row, wrap, flex-start);
	}
}

.header_menu__item {
	@include bp($point_2, min) {
		padding: 0 4.6rem 2rem 0;
	}

	@include bp($point_3) {
		&:not(:last-child) {
			margin-bottom: 3.4rem;
		}
	}
}

.header_menu__link {
	@extend %transition_opacity;

	display: block;

	font-weight: 300;
	letter-spacing: .031em;

	&:hover,
	&:focus {
		text-decoration: none;

		opacity: .8;
	}

	&.header_menu__link--active_mod,
	.current-menu-item & {
		font-weight: 800;
	}

	@include bp($point_2, min) {
		font-size: 1.6rem;

		.body--home_mod & {
			color: $white;
		}

		&:not(.body--home_mod &) {
			color: $black;
		}
	}

	@include bp($point_3) {
		font-size: 2.1rem;
		color: $black;
	}
}

// social
.header_social {
	@include bp($point_2, min) {
		display: none;
	}

	@include bp($point_3) {
		margin-left: auto;
		margin-right: 4px;
	}
}

.header_social__item {
	&:not(:last-child) {
		margin-bottom: 1.4rem;
	}
}

.header_social__link {
	@extend %transition_color_background;

	display: block;

	width: 2.6rem;
	height: 2.6rem;

	padding: .6rem;
	border-radius: 50%;

	border: 1px solid $black_2;
	color: $black_2;

	&:hover,
	&:focus {
		background-color: $black_2;
		color: $white;
	}
}

// button to top
.header_button {
	@extend %transition_opacity;

	display: none;

	@include bp($point_2, min) {
		width: 4.3rem;
		height: 4.3rem;

		border-radius: 50%;
		flex-shrink: 0;

		background-color: $black_2;

		cursor: pointer;

		.body--scroll_state & {
			@include flex_block(row, nowrap, center, center, center);
		}

		&:hover,
		&:focus {
			opacity: .8;
		}
	}
}

.header_button__icon {
	width: 1.3rem;
	height: 1.4rem;
}

// lang
.header_lang {
	position: relative;

	letter-spacing: .01em;
	text-transform: uppercase;

	@include bp($point_2, min) {
		font-size: 1.4rem;

		.body--scroll_state & {
			display: none;
		}
	}

	@include bp($point_3) {
		margin-left: auto;
		margin-bottom: 2rem;

		font-size: 1rem;
	}
}

.header_lang__current {
	border-radius: 50%;
	flex-shrink: 0;

	background-color: $black_2;
	color: $white;
	text-transform: uppercase;
	cursor: pointer;

	@include flex_block(row, nowrap, center, center, center);

	@include bp($point_2, min) {
		width: 4.3rem;
		height: 4.3rem;
	}

	@include bp($point_3) {
		width: 3rem;
		height: 3rem;
	}
}

.header_lang__list {
	position: absolute;
	left: 0;
	top: 0;

	border-radius: 3rem;
	background-color: $black_2;

	opacity: 0;
	pointer-events: none;
	transition: opacity .3s ease, transform .3s ease;

	.header_lang--open_state & {
		opacity: 1;
		pointer-events: initial;

		@include bp($point_2, min) {
			transform: translateY(4.9rem);
		}

		@include bp($point_3) {
			transform: translateY(3.6rem);
		}
	}

	@include bp($point_2, min) {
		width: 4.2rem;
		padding: 4px 4px 2rem 4px;
	}

	@include bp($point_3) {
		width: 3rem;
		padding: 3px 3px 2rem 3px;
	}
}

.header_lang__item {
	@include bp($point_2, min) {
		&:not(:last-child) {
			margin-bottom: 2px;
		}
	}

	@include bp($point_3) {
		&:not(:last-child) {
			margin-bottom: 1.02rem;
		}
	}
}

.header_lang__link {
	@extend %transition_color_background;

	flex-shrink: 0;
	border-radius: 50%;

	color: $white;

	&.header_lang__link--current_mod {
		cursor: default;
		pointer-events: none;

		background-color: $white;
		color: $black_2;
	}

	&:hover,
	&:focus {
		text-decoration: none;
		color: darken($white, 40%);
	}

	@include flex_block(row, nowrap, center, center, center);

	@include bp($point_2, min) {
		width: 3.4rem;
		height: 3.4rem;
	}

	@include bp($point_3) {
		width: 2.4rem;
		height: 2.4rem;
	}
}

// trigger
.header__menu_trigger {
	@include bp($point_2, min) {
		display: none;
	}

	@include bp($point_3) {
		width: 3.4rem;
		height: 3.4rem;
		border-radius: 50%;

		cursor: pointer;

		&.header__menu_trigger--menu_mod {
			position: absolute;
			top: .9rem;
			left: 8.3rem;
		}

		&:not(.body--home_mod &) {
			background-color: $black_2;

			.body--open_menu_state & {
				background-color: transparent;
			}
		}

		@include flex_block(row, nowrap, center, center, center);
	}
}

.header__menu_icon {
	position: relative;

	width: 1.5rem;
	height: 1.2rem;

	&::before,
	&::after,
	span {
		position: absolute;
		left: 0;
		top: 50%;

		width: 100%;
		height: 1px;

		transition: transform .3s ease;
		will-change: transform;

		background-color: $white;

		.header__menu_trigger--menu_mod & {
			background-color: $black_2;
		}
	}

	&::before {
		content: "";
		transform: translateY(-6.4px);
	}

	span {
		transform: translateY(-100%);
	}

	&::after {
		content: "";
		transform: translateY(4.8px);
	}

	.body--open_menu_state & {
		span {
			transform: scale(0);
		}

		&::before {
			transform: rotate(-45deg);
		}

		&::after {
			transform: rotate(45deg);
		}
	}
}
