.hero {
	overflow: hidden;

	&:after {
		z-index: -2;
		position: absolute;
		top: 0;
		left: 0;

		display: block;
		width: 100%;
		height: 100%;
		background-color: $black_2;

		content: '';
	}

	@include bp($point_2, min) {
		position: fixed;
		left: 0;
		top: 0;
		padding-top: 0;
		padding-bottom: 0;
	}

	@include bp($point_3) {
		z-index: 10;
		position: relative;
		// top: var(--height-header);

		min-height: clamp(70.1rem, calc(var(--full-screen) - var(--height-header)), 228rem);
		padding-top: 2rem;
		padding-bottom: 9rem;

		@include flex_block(row, nowrap, center, center);
	}
}

.hero__in {
	position: relative;
	@include flex_block(row, nowrap, center, center);

	@include bp($point_2, min) {
		min-height: 100vh;
		padding-top: 7rem;
		padding-bottom: 7rem;
	}
}

.hero__bg {
	z-index: -1;
	position: absolute;
	overflow: hidden;
	top: 0;

	@include bp($point_2, min) {
		left: var(--gl-indent);

		width: 100%;
		height: 100%;
		//width: max(148.44vh, calc(100% - var(--gl-indent)));
		//height: max(100vh, calc(73vw - var(--gl-indent)));

		&.hero__bg--mobile {
			display: none;
		}
	}

	@include bp($point_3) {
		left: 0;
		bottom: 0;
		top: auto;
		left: 0;

		height: max(clamp(70.1rem, calc(100vh - var(--height-header)), 138vw), 169vw);
    width: clamp(100%, 52vh, 300vh);

		&.hero__bg--desktop {
			display: none;
		}
	}
}

.hero__bg_cover {
	position: absolute;
	left: -30rem;
	top: 0;
	width: 30rem;
	height: 100%;

	background-color: $black_2;
}

.hero__bg_letter {
	z-index: 2;
	position: absolute;
	left: 0rem;
	bottom: 0rem;
	width: auto;
	height: 81%;
}

.hero__bg_in {
	width: 100%;
	height: 100%;
}

.hero__bg_svg_letter{
	@extend %cover_img;
}

.hero__bg_svg_blur {
	background-color: $black;
}

.hero__bg_in_svg {
	position: relative;
	@extend %cover_img;
	z-index: 3;
}

.hero__bg_in_svg_bg {
	position: absolute;
	top: 0;
	left: 0;

	will-change: opacity;

	@extend %cover_img;

	&.hero__bg_in_svg_bg--v2_mod {
		z-index: 2;
	}
}

.hero__bg_svg_blur,
.hero__bg_svg {
	display: block;
	position: absolute;
	width: 103%;
	height: 103%;
	left: 50%;
	top: 50%;
	transform: translate3d(-50%, -50%, 0);
}

.hero__decor {
	z-index: 1;
	position: absolute;
	color: $white;

	&:not(:last-child) {
		width: 1.4rem;
		height: 1.4rem;

		@include bp($point_3) {
			display: none;
		}
	}

	&.hero__decor--pos_0_mod {
		top: 16%;
		right: 11%;
	}

	&.hero__decor--pos_1_mod {
		top: calc(100% - 33.6rem);
		right: 11%;
	}

	&.hero__decor--pos_2_mod {
		top: calc(100% - 7.4rem);
		right: 59.5%;
	}

	&.hero__decor--pos_3_mod {
		width: 1.6rem;
		height: 2.8rem;

		@include bp($point_2, min) {
			top: calc(100% - 8.1rem);
			left: 3.1%;
		}

		@include bp($point_3) {
			top: calc(100% - 10.4rem);
		}
	}
}


.hero__content {
	max-width: 73rem;

	@include bp($point_2, min) {
		margin-bottom: 2.2rem;
		margin-left: 2rem;
	}
}

.hero__title {
	@extend %h1;
	color: $white;
	will-change: filter;

	@include bp($point_2, min) {
		margin-bottom: 3rem;

		//.body--safari_state & {
		//	line-height: 1.55;
		//}
	}

	@include bp($point_3) {
		margin-bottom: 3.2rem;
	}
}

.hero__subtitle {
	letter-spacing: 0.032rem;
	color: $white;
	font-weight: 800;

	@include bp($point_2, min) {
		padding-left: 23rem;
		font-size: 3.2rem;

		//.body--safari_state & {
		//	line-height: 1.55;
		//}
	}

	@include bp($point_3) {
		font-size: 2.1rem;
		letter-spacing: 0.021rem;
	}
}
