.quote_hero {
	z-index: 2;
	@include bp($point_2, min) {
		padding: 2.4rem 13.6rem 7rem var(--gl-indent);
	}

	@include bp($point_3) {
		padding: 1rem 5rem 4rem 7.3rem;
	}
}

.quote_hero_in {
	max-width: 100%;
}

.quote_hero__block {
	background-color: $black_2;
	overflow: hidden;

	@include flex_block(row, wrap, flex-start, stretch, flex-start);

	@include bp($point_2, min) {
		margin-right: -13.6rem;
	}

	@include bp($point_3) {
		flex-direction: column;
		margin-right: -5rem;
	}
}

.quote_hero__block_info {
	@include flex_block(row, wrap, flex-start, flex-start, center);

	@include bp($point_1, min) {
		width: 60.2%;
		padding: 10.6rem 10.3rem 10rem 11.3rem;
	}

	@include bp($point_1 - 1, $point_2) {
		width: 60.2%;
		padding: 6.6rem 10.3rem 6rem 11.3rem;
	}

	@include bp($point_3) {
		width: 100%;
		padding: 5.4rem 7.3rem 5.6rem 5rem;
		order: 2;
	}
}

.quote_hero__block_info_text {
	@include bp($point_2, min) {
		margin-bottom: 9.2rem;
	}

	@include bp($point_3) {
		margin-bottom: 3.6rem;
	}
}

.quote_hero__block_info_icon {
	position: absolute;
	overflow: hidden;

	@include bp($point_2, min) {
		top: .3rem;
		left: .2rem;

		margin: 0 2rem 1rem 0;
	}

	@include bp($point_3) {
		top: .5rem;

		margin: 0 1.2rem .5rem 0;
	}
}

.quote_hero__block_info_icon_in {
	@include bp($point_2, min) {
		width: 2.1rem;
		height: 1.4rem;
	}

	@include bp($point_3) {
		width: 1.5rem;
		height: 1rem;
	}
}

.quote_hero__block_info_text_in {
	position: relative;
	p {
		font-weight: 300;
		color: $white;

		@include bp($point_2, min) {
			font-size: 3rem;
			text-indent: 4.4rem;
			letter-spacing: .01em;
			line-height: 1.18;
		}

		@include bp($point_3) {
			font-size: 2.1rem;
			text-indent: 3.1rem;
			letter-spacing: .006em;
			line-height: 1.36;
		}
	}
}

.quote_hero__block_info_bottom {
	position: relative;
	width: 100%;

	@include bp($point_2, min) {
		@include flex_block(row, wrap, flex-start, center, flex-start);
	}

	@include bp($point_3) {
		@include flex_block(column, wrap, flex-start, flex-end, flex-end);
	}
}

.quote_hero__block_info_backstage {
	@extend %stretch;

	width: 100%;
	height: 100%;

	pointer-events: none;
	background-color: #1e1e1e;

	@include bp($point_3) {
		display: none;
	}

	&.quote_hero__block_info_backstage--hide_state {
		display: none;
	}
}

.quote_hero__block_info_signature {
	color: $white;

	@include bp($point_2, min) {
		width: 70%;
		//height: 7.3rem;
	}

	@include bp($point_3) {
		width: 17.5rem;
		height: 3.6rem;

		margin-bottom: 3rem;
	}
}

.quote_hero__block_info_signature_img {
	// @extend %cover_img;
	@extend %contain_img;
}

.quote_hero__block_info_links {
	@extend %global_flex_block_row_wrap_flex-start_baseline;

	@include bp($point_2, min) {
		width: 30%;
		margin-top: -1rem;
	}

	@include bp($point_3) {
		//align-items: center;
		width: 100%;
		font-size: 2.2rem;
	}
}

.quote_hero__block_info_label {
	margin-left: auto;

	font-weight: 800;
	line-height: 1.1;
	color: $gray_2;

	@include bp($point_2, min) {
		font-size: 2.9rem;
	}

	@include bp($point_3) {
		font-size: 2.2rem;
	}
}

.quote_hero__block_info_social {
	@extend %transition_background;
	color: $black;

	background-color: $white;
	border-radius: 50%;

	@include bp($point_2, min) {
		width: 2.5rem;
		height: 2.5rem;

		margin-left: 1rem;
		padding: .7rem;
	}

	@include bp($point_3) {
		width: 1.8rem;
		height: 1.8rem;

		margin-left: 1rem;
		margin-top: 1px;
		padding: .5rem;
	}

	&:hover,
	&:focus {
		background-color: darken($white, 20%);
	}
}

.quote_hero__block_img_w {
	display: block;

	@include bp($point_1, min) {
		min-height: 55rem;
	}

	@include bp($point_2, min) {
		width: 39.8%;
	}

	@include bp($point_3) {
		width: 100%;
		order: 1;
	}
}

.quote_hero__block_img {
	@extend %cover_img;
}
